html {  margin-top: 46px !important; }

body.admin-bar .show-admin-bar {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #000;
    background-color: rgba(0, 0, 0, 0.8);
    text-align: left;
    color: #FFF;
    padding: 10px;
    z-index: 1000;
    font-size: 12px;
    font-family: sans-serif;

    p { margin: 0; }
}


